import React from 'react';

import { Input, Button, Form } from 'antd';

import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';
import { FormItem, Label } from '../style';

const skills = () => {
  const onFinish = (values) => {
    // console.log('Received values of form:', values);
  };

  return (
    <Form
      name="dynamic_form_item"

      onFinish={onFinish}
    >
      <Form.List name="names">
        {(fields, { add, remove }) => (
          <div>
            {fields.map((field, index) => (
              <Form.Item


                required={false}
                key={field.key}
              >
                <Form.Item
                  {...field}
                  validateTrigger={['onChange', 'onBlur']}

                  noStyle
                >
                  <Input placeholder="skills" style={{ width: 270 }} />
                </Form.Item>
                {fields.length > 1 ? (
                  <MinusCircleOutlined
                    className="dynamic-delete-button"
                    style={{ margin: '0 8px' }}
                    onClick={() => {
                      remove(field.name);
                    }}
                  />
                ) : null}
              </Form.Item>
            ))}
            <Form.Item>
              <Button
                type="dashed"
                onClick={() => add()}
                style={{ width: '100%' }}
              >
                <PlusOutlined />
                {' '}
                Add skill
              </Button>
            </Form.Item>
          </div>
        )}
      </Form.List>
    </Form>
  );
};

export class SkillsList extends React.Component {
  render() {
    const { id, labelName } = this.props;
    return (
      <FormItem>
        <Label htmlFor={id}>{labelName}</Label>
        {skills()}
      </FormItem>
    );
  }
}
