import * as Yup from 'yup';

export const BasicFormSchema = Yup.object().shape({
  email: Yup.string().email('Invalid email address').required('Required'),
  name: Yup.string()
    .min(2, 'Must be longer than 2 characters')
    .max(50, 'Nice try, nobody has a first name that long')
    .required('Required'),
  password: Yup.string()
    .min(8, 'Must be longer than 8 characters')
    .max(40, 'Nice try, nobody has a first name that long')
    .matches(/[a-z]*[0-9]+[A-Z]+/, 'matches')
    .required('Required'),
  age: Yup.number()
    .min(18, 'Must be longer than 8 characters')
    .max(65, 'Nice try, nobody has a first name that long')
    .required('Required'),
  website: Yup.string().url('Invalid website address'),
  repeatPassword: Yup.string()
    .oneOf([Yup.ref('password'), null], 'password must match')
    .required('Required'),
  acceptTerms: Yup.boolean()
    .required('Required')
    .oneOf([true], 'Must Accept Terms and Conditions'),
});
