// console.log(axios.get('http://localhost:5000/sign-up'))


const SignupForm = () => {
  const formik = useFormik({
    initialValues: {
      name: '',
      password: '',
      repeatPassword: '',
      email: '',
      website: '',
      age: '',
      skills: '',
      acceptTerms: '',
    },
    onSubmit: (values) => {
      // console.log('onSubmit', values);
    },
    validationSchema: BasicFormSchema,
  });
  // console.log('formik.values', formik.values);


  return (
    <>
      <Form onSubmit={formik.handleSubmit}>
        <Name
          id="name"
          labelName="Name"
          onChange={formik.handleChange}
          value={formik.values.name}
        />
        {formik.touched.name && formik.errors.name ? (
          <div>{formik.errors.name}</div>
        ) : null}
        <Password
          id="password"
          labelName="Password"
          onChange={formik.handleChange}
          value={formik.values.password}
        />
        {formik.touched.password && formik.errors.password ? (
          <div>{formik.errors.password}</div>
        ) : null}
        <Password
          id="repeatPassword"
          labelName="Repeat Password"
          onChange={formik.handleChange}
          value={formik.values.repeatPassword}
        />
        {formik.touched.repeatPassword && formik.errors.repeatPassword ? (
          <div>{formik.errors.repeatPassword}</div>
        ) : null}
        <Email
          id="email"
          labelName="Email Address"
          onChange={formik.handleChange}
          value={formik.values.email}
        />
        {formik.touched.email && formik.errors.email ? (
          <div>{formik.errors.email}</div>
        ) : null}
        <Website
          id="website"
          labelName="Website"
          onChange={formik.handleChange}
          value={formik.values.website}
        />
        {formik.touched.website && formik.errors.website ? (
          <div>{formik.errors.website}</div>
        ) : null}
        <Age
          id="age"
          labelName="Age"
          onChange={formik.handleChange}
          value={formik.values.age}
        />
        {formik.touched.age && formik.errors.age ? (
          <div>{formik.errors.age}</div>
        ) : null}
        <SkillsList id="skills" labelName="Skills" />
        <AcceptTerms
          id="acceptTerms"
          labelName="Accept Terms"
          onChange={formik.handleChange}
          checked={formik.values.acceptTerms}
        />
        {formik.touched.acceptTerms && formik.errors.acceptTerms ? (
          <div>{formik.errors.acceptTerms}</div>
        ) : null}
        <Submit labelName="Submit" />
      </Form>
    </>
  );
};

export default SignupForm;
